<template>
    <div class="webshop-home">
        <div class="webshop-menu">
            <div class="menu-ul">
                <div class="menu-item" v-for='(menu,index) in menus' :id="'webshoptab'+index" @click="menuItemClick(index)">
                    <div class="menu-text">{{menu.titleName}}</div>
                    <div class="text-bg"></div>
                </div>
            </div>
        </div>
        <keep-alive>
          <router-view></router-view>
        </keep-alive>
    </div>
</template>

<script>
import { mapState } from "vuex";

export default {
  name: "v-webshop-home",
  data() {
    return {
      menus: [
        { path: "webShopAll", titleName: "全部" },
        { path: "webShopSpecial", titleName: "特价" },
        { path: "webShopIntegral", titleName: "积分" },
        { path: "webShopAdvise", titleName: "推荐" }
      ],
      curIndex: -1
    };
  },
  components: {},
  methods: {
    menuItemClick(index, direct = false) {
      if (!direct && this.curIndex == index) return;
      if (this.curIndex != -1) {
        let lastTab = document.getElementById("webshoptab" + this.curIndex);
        lastTab.classList.remove("active");
      }
      let curTab = document.getElementById("webshoptab" + index);
      curTab.classList.add("active");
      this.curIndex = index;
      let menu = this.menus[index];
      this.$router.replace(menu.path);
    }
  },
  activated() {
    if (this.curIndex == -1) this.menuItemClick(0);
    else this.menuItemClick(this.curIndex, true);
  },
  mounted() {},
  computed: {
    ...mapState(["path", "icon"])
  }
};
</script>

<style lang="stylus" rel="stylesheet/stylus">
.webshop-home {
    font-family: 'Microsoft YaHei';
    width: 100%;

    // 每个导航子页的轮播样式
    .swiper-section {
        width: 100%;

        .swipe-wrapper {
            border-top: 2.5px solid #9fa0a0;
            border-bottom: 2.5px solid #9fa0a0;

            .mint-swipe {
                width: 3.2rem;
                height: 1.6rem;

                .my-img {
                    background-color: #f1f1f1;
                    width: 100%;
                    height: 100%;
                }

                .mint-swipe-indicators {
                    bottom .05rem;
                    .mint-swipe-indicator {
                        background-color: #fff;
                        margin: 0 0.03rem;
                    }

                    .mint-swipe-indicator.is-active {
                        background-color: #f8ef94;
                        opacity: 0.8;
                    }
                }
            }
        }
    }

    // 导航样式
    .webshop-menu {
        height: 0.31rem;
        margin: 0px 0.1rem;

        .menu-ul {
            height: 100%;
            display: flex;
            align-items: center;
            justify-content: center;

            .menu-item {
                flex: 1;
                text-align: center;
                position: relative;

                .menu-text {
                    letter-spacing: 0.5px;
                    font-size: 0.13rem;
                    color: #595656;
                }

                .text-bg {
                    display: none;
                    position: absolute;
                    width: 0.62rem;
                    border-radius: 20px;
                    background: #fff798;
                    height: 0.2rem;
                    left: 50%;
                    top: 50%;
                    transform: translate(-50%, -50%);
                    z-index: -1;
                }
            }

            .active {
                .menu-text {
                    color: #a2733d;
                }

                .text-bg {
                    display: block;
                }
            }
        }
    }
}
</style>



